import React from 'react';

const EditorsPick = () => {
    return (
        <div className="bg-gray-100">
            <section className="max-w-screen-xl mx-auto py-8">
                <h2 className="text-center text-lg font-bold uppercase mb-2">Editor's Pick</h2>
                <p className="text-center text-gray-500 text-sm mb-6">
                    Problems trying to resolve the conflict between
                </p>
                <div className="grid grid-cols-3 gap-4">
                    {/* Men (Large Section, spans 2 columns and 2 rows) */}
                    <div className="col-span-2 row-span-2 relative group">
                        <img
                            src="https://via.placeholder.com/600x800"
                            alt="Men"
                            className="w-full h-full rounded object-cover"
                        />
                        <div className="absolute bottom-4 left-4 bg-white bg-opacity-75 px-4 py-2 text-black text-lg font-bold">
                            Men
                        </div>
                    </div>
                    {/* Women (Top Right) */}
                    <div className="col-span-1 relative group">
                        <img
                            src="https://via.placeholder.com/300x400"
                            alt="Women"
                            className="w-full h-full rounded object-cover"
                        />
                        <div className="absolute bottom-4 left-4 bg-white bg-opacity-75 px-4 py-2 text-black text-lg font-bold">
                            Women
                        </div>
                    </div>
                    {/* Accessories (Middle Right) */}
                    <div className="col-span-1 relative group">
                        <img
                            src="https://via.placeholder.com/300x200"
                            alt="Accessories"
                            className="w-full h-full rounded object-cover"
                        />
                        <div className="absolute bottom-4 left-4 bg-white bg-opacity-75 px-4 py-2 text-black text-lg font-bold">
                            Accessories
                        </div>
                    </div>
                    {/* Kids (Bottom Right) */}
                    <div className="col-span-1 relative group">
                        <img
                            src="https://via.placeholder.com/300x200"
                            alt="Kids"
                            className="w-full h-full rounded object-cover"
                        />
                        <div className="absolute bottom-4 left-4 bg-white bg-opacity-75 px-4 py-2 text-black text-lg font-bold">
                            Kids
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
};

export default EditorsPick;

